import React from 'react'

function Features() {
    return (
        <div className="container">
            <div className="features">
            <div className="card features_text">
                    <h6>OUR TIPS FOR YOU</h6>
                    <h4>Important features </h4>
                    <p>Lorem ipsum dolor sit amet, coectetur adipiscing elit. Suspendisse varius enim in eros</p>

                </div>

                <div className="features__imgBox">
                    <div className="img img1"><p>120 million curious readers and growing</p></div>
                    <div className="img img2"><p>Expand your reading. Expand your mind.</p></div>
                </div>
            </div>
        </div>
    )
}

export default Features



