import React from 'react'

function Plan() {
    return (
        <div className="container">
            <div className="plan">
            <div className="card card_text ">
                    
                    <h4 >Choose the plan thats right for your business</h4>
                    <p>Lorem ipsum dolor sit amet, coectetur adipiscing elit. Suspendisse varius enim in eros</p>

                </div>
                <div className="plan__grid">
                    <div className="pricing pricing1">
                        <p className="pricing__name">Started Plan</p>
                        <div className="pricing__price pricing__price--1">Free</div>
                        <p className="pricing__month">per month</p>
                        
                        <p className="pricing__description">Lorem ipsum dolor sit amet, coectetur adipiscing elit.  </p>
                        <button className="pricing__button">Get Started</button>
                    </div>
                    <div className="pricing pricing2">
                        <p className="pricing__name">Enterprise Plan</p>
                        <div className="pricing__price pricing__price--2">$20.00</div>
                        <p className="pricing__month">per month</p>
                        
                        <p className="pricing__description">Lorem ipsum dolor sit amet, coectetur adipiscing elit. </p>
                        <button className="pricing__button">Get Started</button>
                    </div>
                    <div className="pricing pricing3">
                        <p className="pricing__name">Enterprise plan</p>
                        <div className="pricing__price pricing__price--3">$50.00</div>
                        <p className="pricing__month">per month</p>
                        
                        <p className="pricing__description">Lorem ipsum dolor sit amet, coectetur adipiscing elit. </p>
                        <button className="pricing__button pricing__button--1 ">Get Started</button>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Plan



