import React, {Component} from 'react'

class NavbarResponsive extends Component  {

    state = {
        navbar: ''
    }

    toggleNav = ()=> {

        this.setState({navbar : this.state.navbar == 'active' ? '' : 'active'})
        
    }

    render (){
        const {navbar} = this.state
        const mobileNav =  navbar == 'active' ? 'mobile-nav' : ''
        const isActive = navbar == 'active' ? 'is-active' : ''

        return (
        
            <div class="nav-wrapper">
            <div class="grad-bar"></div>
            <nav className="navbar2">
              <div className="img"> </div>
              <div onClick={this.toggleNav} className={`menu-toggle ${isActive}`} id="mobile-menu">
                <span className="bar"></span>
                <span className="bar"></span>
                <span className="bar"></span>
              </div>
              <ul class="nav no-search">
                <li className="nav-item"><a href="#">Home</a></li>
                <li className="nav-item"><a href="#">About</a></li>
                <li className="nav-item"><a href="#">Work</a></li>
                <li className="nav-item"><a href="#">Careers</a></li>
                <li className="nav-item"><a href="#">Contact Us</a></li>
                <i className="fas fa-search" id="search-icon"></i>
                <input className="search-input" type="text" placeholder="Search.."/>
              </ul>
            </nav>
            </div>
              )
    }
   
}

export default NavbarResponsive



