import React, {Component, Fragment} from "react";
import {CSSTransition} from "react-transition-group";



import NavBar from "./components/Navbar";
import Hero from "./components/Hero";
import Details from "./components/Details";
import Features from "./components/Features"
import Plan from './components/Plan';
import Background from "./components/Background";
import NavbarResponsive from "./components/NavbarResponsive";


class App extends Component {
    state = {
        page: "hero",
        navbar: true,
        count: 0,
        status: 'beginning'
       

    }

    pages = ['hero', 'details', 'features', 'plan', 'background']

    render() {
        // conditional glowing buttons
        const {status} = this.state
        let glowNext = "navigator navigator--next";
         glowNext +=  status == 'beginning' || status == 'middle' ? ' glowNext' : '';

         let glowPrev = "navigator navigator--prev";
         glowPrev += status == 'middle' || status == 'end' ? ' glowPrev': '';
        

        return (
            <div className="app">
         <CSSTransition  in={this.state.navbar} timeout={{enter: 1000, exit: 200}} classNames="NAV"> 
           <NavBar/>
        </CSSTransition>
       
        <CSSTransition  in={this.state.status == 'end'} timeout={{enter: 3000, exit: 3000}} classNames="ALERT" mountOnEnter={true} unmountOnExit={true}> 
           <div className="alert" > Welcome to the End, Courtesy DAVIK</div>
        </CSSTransition>
        <CSSTransition   in={this.state.page=="hero"} onEnter={()=> this.setState({status: 'beginning'})}  onExit={()=> this.setState({status: 'middle'})}  timeout={{enter: 1000, exit: 500}} classNames="HERO" unmountOnExit={true} mountOnEnter={true} >
           <Hero/>
        </CSSTransition>
        <CSSTransition in={this.state.page=="details"} timeout={{enter: 1000, exit: 500}} classNames="DETAILS" unmountOnExit={true} mountOnEnter={true}>
           <Details/>
        </CSSTransition>
        <CSSTransition in={this.state.page=="features"} timeout={{enter: 200, exit: 200}} classNames="FEATURES" unmountOnExit={true} mountOnEnter={true}>
           <Features/>
        </CSSTransition>
        <CSSTransition in={this.state.page=="plan"} timeout={{enter: 1000, exit: 200}} classNames="PLAN" unmountOnExit={true} mountOnEnter={true}>
           <Plan/>
        </CSSTransition>
        <CSSTransition  onEnter={()=> this.setState({status: 'end'})} onExit={()=> this.setState({status: 'middle'})} in={this.state.page=="background"} timeout={{enter: 1000, exit: 500}} classNames="BACKGROUND" unmountOnExit={true} mountOnEnter={true}>
          <Background/>
        </CSSTransition>
        <div className="navigatorBox">
            <button className={glowPrev} onClick={()=> {
                      const count = Math.max(this.state.count - 1, 0)
                      this.setState({
                          count
                      })
                      this.setState({
                          page: this.pages[count]
                      })

              
            }}>
                Prev
            </button>

            <button className={glowNext } onClick={()=> {
                     const count =  Math.min(this.state.count + 1, this.pages.length -1)
                     this.setState({
                         count
                     })
                     this.setState({
                         page: this.pages[count]
                     })

              
            }}>
                Next
            </button>
            </div>
           </div>
        );
    }
}



export default App;



